<template>
  <div class="body">
    <div class="container">
      <h2>Vue Intl Telephone Input Demo</h2>
      <div class="box" v-show="validate">
        <p>countryCode: {{ countryCode }}</p>
        <p>number: {{ number }}</p>
      </div>
      <div class="box">
        <intl-tel-input :options="intlOtipns" @validateSuccess="onSuccess" @validateError="onError"></intl-tel-input>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
  	return {
  		intlOtipns: {
        separateDialCode: false,
        customCss: '',
        input: {
          required: false,
          readonly: false
        }
      },
      validate: false,
      number: '',
      countryCode: '',
  	}
  },
  methods: {
    onSuccess({ number, countryCode }) {
      this.validate = true;
      this.countryCode = countryCode;
      this.number = number;
    },
    onError() {
      this.validate = false;
    }
  }
};
</script>

